:root::before,
:root::after {
  position: absolute;
  bottom: 0;
  z-index: -1;
  height: 20vh;
  text-align: center;
  font: 900 4vw / 20vh monospace;
}
:root::after {
  content: 'OFF AIR';
  right: 0;
  width: 30vw;
  color: #fff;
  text-shadow: 0 0 5px #000;
  background: url('https://upload.wikimedia.org/wikipedia/commons/6/66/SMPTE_Color_Bars.svg') no-repeat 50% 50% / 100%;
}
:root::before {
  content: attr(data-countdown);
  width: 50vw;
}
body {
  margin: 0;
  display: flex;
  overflow: hidden;
}
iframe {
  width: 70vw;
  height: 42vw;
  max-height: 80vh;
}
iframe:nth-of-type(2) {
  width: 30vw;
  height: 100vh;
  max-height: 100vh;
}
iframe:nth-of-type(1):nth-last-of-type(1) {
  flex: 1;
  height: 100vh;
}
@media (orientation: portrait) {
  :root::after {
    width: 50vw;
    font-size: 8vw;
    background-size: cover;
  }
  body {
    flex-direction: column;
  }
  iframe {
    width: 100vw;
    height: 60vh;
    flex-grow: 6;
  }
  iframe:nth-of-type(2) {
    width: 100vw;
    height: 40vh;
    flex-grow: 4;
  }
}
